@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Thin.woff2") format("woff2");
  font-weight: 100;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Bold.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

header {
  padding: 18px;
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr 3fr;
  flex-direction: row;
  line-height: 1;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}
header a {
  display: inline-block;
  font-family: "Metric";
  font-size: 17px;
  font-weight: 500;
  color: #284f38;
  letter-spacing: 0.035em;
}
header a span {
  color: #9d9d9d;
}
header nav ul {
  display: flex;
}
header nav a {
  font-family: "Metric";
  font-size: 17px;
  font-weight: 300;
  color: #000;
  padding-right: 17px;
  letter-spacing: 0.035em;
}